.loader {
    position: absolute;
    z-index: 1000;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 2rem;
    background: black;
    color: white;
    font-family: 'Inter', Arial, sans-serif;
}
